// For icons
.size(@n) {
		font-size: @n;
		width: @n;
}


.roboto() {
	font-family: 'Roboto', sans-serif;
}

.sans() {
	font-family: 'PT Sans', sans-serif;
}

.hide {
	display: none;
}

.no-break {
	white-space: nowrap;
}
.placeholder(@rules) {
	&::-webkit-input-placeholder {
		@rules();
	}
	&:-moz-placeholder {
		@rules();
	}
	&::-moz-placeholder {
		@rules();
	}
	&:-ms-input-placeholder {
		@rules();
	}
}