.rev-form{
	margin-top: 10px;
	font-size: 14px;
}
.rev-form__title{
	margin-top: 0;
}
.rev-form__input{
	.placeholder({color: #999});
	border: none;
	border-bottom: 2px solid #E5E5E5;
	color: #333;
	width: 100%;
	line-height: 30px;
	.placeholder({color: #B8B8B8});
}
.rev-form__group{
	margin: 0 0 1.5em;
	padding: 0;
	border: none;
}
.rev-form__group-half{
	display: inline-block;
	width: 50%;
	&:first-child{
		padding-right: 10px;
	}
	&+&{
		padding-left: 10px;
	}
}
.rev-form__rating{
	margin: 0 0 1.5em;
}
.rev-form__stars{
	line-height: 0;
	vertical-align: middle;
	display: inline-block;
	margin-left: 1em;
	div{
		display: inline-block;
	}
}
.rev-form__text{
	height: 160px;
	width: 100%;
	resize: vertical;
	border: 2px solid #E5E5E5;
}
.rev-form__info{
	font-size: 12px;
	margin: 1.5em 0;
}
.rev-form__send{
	line-height: 26px;
}