.prod-description{
	position: relative;
	color: #737373;
	
	font-size: 14px;
}
.prod-description__top{
	
}
.product__old-price{
	
}
.product__price{
	
}
.prod-description__delimeter{
	margin: 1.5em 0;
	border: none;
	border-top: 1px solid #E5E5E5;
}
.prod-description__featured{
	width: 100%;
	display: table;
	margin: 1.5em 0;
	table-layout: fixed;
}
.prod-description__featured-col{
	display: table-cell;
	width: 50%;
	vertical-align: middle;
	font-size: 14px;
	&.icon{
		width: 50px;
		line-height: 0;
	}
	&.text{
		padding-left: 12px;
	}
}
.prod-description__list{
	list-style-type: none;
	margin: 0;
	padding: 0;
	column-count: 2;
	margin-bottom: 1.5em;
	font-size: 14px;
}
.prod-description__item{
	padding-left: 20px;
	position: relative;
	margin-bottom: 1em;
	&:before{
		content: '';
		color: inherit;
		display: inline-block;
		position: absolute;
		top: 2px;
		left: 0;
		width: 10px;
		height: 10px;
		border: 2px solid #B1BAC1;
		border-radius: 10px;
	}
}
