.wish-btn {
	position: absolute;
	top: 20px;
	right: 19px;
	border: solid 2px #fff;
	border-radius: 15px;
	height: 30px;
	cursor: pointer;
	padding: 0 17px;
	transition: right .3s, margin .3s;
	margin: 0;
	z-index: 5;
	&.checked {
		right: 50%;
		margin-right: -(85px / 2);
	}
	&__checkbox {
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	}
	&__text {
		line-height: 26px;
		font-size: 14px;
		font-weight: 500;
		color: #fff;
	}
	&_static{
		position: relative;
		display: inline-block;
		top: 0;
		left: 0;
	}
}