.icon {
	display: inline-block;
	width: 20px;
	height: 20px;
	line-height: 30px;
	vertical-align: middle;
	fill: #fff;
}
.icon-search {

}
.icon-cart {

}
.icon-twitter {
	width: 30px;
	height: 30px;
	display: inline-block;
	background: url('../img/icons/tw.svg');
}
.icon-facebook {
	width: 30px;
	height: 30px;
	display: inline-block;
	background: url('../img/icons/fb.svg');
}
.icon-vk {
	width: 30px;
	height: 30px;
	display: inline-block;
	background: url('../img/icons/vk.svg');
}

.no-svg .icon-twitter {
	background: url('../img/png-icons/icons/tw.png');
}
.no-svg .icon-facebook {
	background: url('../img/png-icons/icons/fb.png');
}
.no-svg .icon-vk {
	background: url('../img/png-icons/icons/vk.png');
}