.photoview{
	margin-bottom: 20px;
}
.photoview__title{
	.product__pb-title();
}
.photoview__gallery{
	position: relative;
	padding-bottom: 99%;
}
.photoview__current{
	position: absolute;
	border: none;
	background: none;
	padding: 0;
	text-align: center;
	margin: 0 -30px 10px;
	display: none;
	top: 0;
	left: 0;
	&.active{
		display: block;
	}
}
.photoview__expand{
	position: absolute;
	top: 30px;
	left: 30px;
	line-height: 20px;
	opacity: .4;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
	color: #374147;
	transition: opacity .2s ease-in-out;
	height: 500px;
	span{
		font-size: 14px;
		vertical-align: middle;
	}
	.photoview__current:hover &{
		opacity: 0.5;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	}
}
.photoview__expand-svg{
	vertical-align: middle;
	margin-right: 5px;
	fill: currentColor;
}
.photoview__cur-img{
	display: table-cell;
	vertical-align: middle;
}
.photoview__list{
	list-style: none;
	margin: 0 0 -10px;
	padding: 0;
	text-align: center;
	font-size: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
}
.photoview__item{
	display: inline-block;
	vertical-align: top;
}
.photoview__link{
	display: block;
	font-size: 16px;
	line-height: 0;
	padding: 10px;
}
.photoview__img{}