.autocomplete{
	display: none;
	position: absolute;
	top: 50px;
	width: 100%;
	padding: 0;
	&__list{
		padding: 0;
		margin: 0;
		list-style-type: none;
	}
	&__item{
		margin: 0;
		position: relative;
		white-space: nowrap;
		
		& + .autocomplete__item{
			border-top: 1px solid #e1e0e1;
		}
	}
	&__link{
		height: 50px;
		display: block;
		background: #fff;
		position: relative;
		padding: 18px 70px 20px 18px;
		.transition(all .4s);
		overflow: hidden;
		text-overflow: ellipsis;
		-o-text-overflow: ellipsis;
		font-size: 14px;
		color: #000;
		.icon-chevron-right{
			background: url('../img/icons/big_arrow.svg') no-repeat;
			position: absolute;
			display: block;
			top: 50%;
			margin-top: -10px;
			right: 20px;
			width: 20px;
			height: 20px;
		}
		&:hover{
			background: #f8c255;
		}
	}
}