html{
	font-size: 16px;
}
body {
	.roboto();
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	line-height: 1.3;
	color: #333;
	position: relative;
}

// Selection color
::selection {
  background: @selection-color; /* WebKit/Blink Browsers */
}

::-moz-selection {
  background: @selection-color; /* Gecko Browsers */
}

a {
	text-decoration: none;
}

* {
	box-sizing: border-box;
}

a,
input,
select,
button,
textarea {
	&:focus {
		outline: none;
	}
}

h3{
	font-size: 18px;
	line-height: 20px;
	font-weight: 400;
}
h2{
	font-size: 24px;
	line-height: 1.3;
	font-weight: 300;
	color: #333;
}

.container {
	width: 1180px;
	margin: 0 auto;
	.clearfix();
}

.content {
	background: #e9eae2;
	padding-top: 40px;
	[class^="content__column"] {
		padding: 0 (@gutter / 2);
		float: left;
	}
	&__row {
		.clearfix();
		margin-left:  (@gutter / -2);
		margin-right: (@gutter / -2);
		margin-bottom: @gutter;
	}
	&__row2col{
		margin-left:  (@gutter / -2);
		margin-right: (@gutter / -2);
		margin-bottom: @gutter;
		column-count: 2;
		column-gap: 0;
	}
	&__col{
		padding: 0 (@gutter / 2);
		width: 100%;
		display: inline-block;
	}
	&__column-1 {
		width: (100% / 4) * 1;
	}
	&__column-2 {
		width: (100% / 4) * 2;
		.perspective(10000px);
		position: relative;
		height: 350px;
	}
	&__column-3 {
		width: (100% / 4) * 3;
	}
	&__column-4 {
		width: (100% / 1) * 4;
	}
}
.responsive-video{
	position: relative;
	padding-bottom: (9/16)*100%;
	height: 0;
	iframe{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}