@keyframes loader-show{
	0%{
		stroke-width: 4px;
		stroke: #ADB7BE;
		transform: scale(0.5);
		stroke-dashoffset: 62;
	}
	100%{
		transform: scale(1);
		stroke: #F8C255;
		stroke-width: 2px;
		stroke-dashoffset: 0;
	}
}
@keyframes loader-hide{
	0%{
		color: red;
	}
	100%{
		color: green;
	}
}
.carousel {
	width: 100%;
	height: 350px;
	.clearfix();
	&__controls {
		width: 80px;
		height: 350px;
		background: #fff;
		float: left;
		text-align: center;
	}
	&__controls-list {
		list-style: none;
		padding: 0;
		margin: 0;
		height: 100%;
		display: flex;
		flex-direction: column;
		padding: 26px 0 50px;
	}
	&__controls-item {
		height: 42px;
		margin: auto;
	}
	&__progress {
		display: block;
	}
	&__svg{
		display: block;
		margin: -2px auto 6px;
		width: 24px;
		height: 24px;
		fill: none;
		stroke-width: 4px;
		stroke: #ADB7BE;
		transform: scale(0.5);
		stroke-dasharray: 62;
	}
	.carousel__progress.active &__svg{
		animation: loader-show 5s;
		transform: scale(1);
		stroke: #F8C255;
		stroke-width: 2px;
	}
	&__controls-label {
		font-size: 12px;
		line-height: 1;
		color: #545454;
		opacity: 0.7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		transition: color .3s, fill .3s;
	}
	.carousel__progress.active &__controls-label{
		opacity: 1;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
	}
	&__frame {
		display: inline-block;
		width: 800px;
		height: 350px;
		overflow: hidden;
	}
	&__list {
		.clearfix();
		list-style: none;
		padding: 0;
		margin: 0;
	}
	&__item {
		overflow: hidden;
	}
	&__img {
		float: left;
		width: 510px;
		height: 350px;
	}
	&__container {
		width: 290px;
		height: 350px;
		float: right;
		background: #F8C255;
		padding: 0 30px;
		display: flex;
		flex-direction: column;
	}
	&__title {
		font-size: 18px;
		font-weight: 400;
		color: #010101;
		line-height: 20px;
		margin: auto 0 10px;
	}
	&__info-list {
		list-style: none;
		padding: 0;
		margin: 10px 0 auto;
	}
	&__info-item {
		color: #000;
		opacity: 0.7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		border-top: 1px solid #DFAE4C;
		padding: 20px 0;
	}
	&__info-img {
		fill: currentColor;
		width: 32px;
		height: 32px;
		float: left;
	}
	&__info-text {
		padding-left: 50px;
		margin: 0;
		font-size: 14px;
	}
}