.catalog{
	&__heading{
		margin: 0 0 20px;
		height: 30px;
		line-height: 30px;
	}
	&__header{
		font-size: 14px;
		line-height: 30px;
		margin-bottom: 20px;
	}
}
.catalog__list{
	margin: 0;
	padding: 0;
	list-style: none;
	overflow: hidden;
}
.catalog__item{
	float: left;
	width: (100/3)*1%;
	margin-bottom: 20px;
	&:nth-child(3n){
		padding-left: 10px;
	}
	&:nth-child(3n-1){
		padding: 0 10px;
	}
	&:nth-child(3n-2){
		padding-right: 10px;
	}
}
.catalog__{}