.article{
	
}
.article__heading{
	background: #334C5D;
	color: #fff;
	font-weight: 500;
	min-height: 260px;
	overflow: hidden;
	position: relative;
}
.article__head_img{
	opacity: .5;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
	position: absolute;
	z-index: 1;
	width: 100%;
}
.article__head_main{
	position: relative;
	z-index: 2;
	padding: 26px 30px;
}
.article__category{
	font-size: 14px;
	text-transform: uppercase;
	font-style: italic;
	margin-bottom: 70px;
}
.article__title{
	font-size: 26px;
	line-height: 30px;
	font-weight: 500;
	margin-bottom: 60px;
}
.article__text{
	background: #fff;
	padding: 36px 30px;
	font-size: 14px;
	line-height: 1.5;
	p:first-child{
		margin-top: 0;
	}
	blockquote{
		margin: 1.6em 0;
		font-size: 1.4em;
		line-height: 1.1;
	}
}


.to_read{
	background: #fff;
	margin-bottom: 40px;
}
.to_read__title{
	margin: 0;
	padding: 28px 30px;
}
.to_read__list{
	list-style: none;
	margin: 0;
	padding: 0;
}
.to_read__item{
	border-top: 1px solid #E5E5E5;
	&.active,
	&.active + &{
		border-color: #DFAE4C;
	}
}
.to_read__link{
	display: block;
	color: #333;
	padding: 16px 30px;
	font-size: 14px;
	line-height: 1.5;
	.to_read__item.active &{
		background: #F8C255;
		color: #000;
	}
}
.to_read__icon{}