.product {
	height: 350px;
	background: #7dd5c9;
	position: relative;
	overflow: hidden;
	&__img {
		width: 100%;
		padding: 0 50px;
	}
	&__flag {
		font-size: 12px;
		color: #e9eae2;
		background: url('../img/flag.svg') no-repeat;
		background-size: contain;
		width: 70px;
		height: 60px;
		display: inline-block;
		position: absolute;
		top: 0;
		left: 7px;
		text-align: center;
		z-index: 5;
	}
	&__flag_right{
		left: auto;
		right: 30px;
	}
	&__flag-title {
		width: 70px;
		margin-top: 8px;
		margin-bottom: 18px;
	}
	&__flag-value {
		font-weight: 500;
		width: 70px;
	}
	&__title {
		font-size: 14px;
		line-height: 20px;
		color: #fff;
		font-weight: normal;
		margin: 0;
	}
	&__title a{
		color: #fff;
	}
	&__title a:hover{
		text-decoration: underline;
	}
	&__text {

	}
	&__info {
		background: #15b287;
		opacity: .9;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
		height: 150px;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		transition: bottom .3s, height .3s;
		padding: 15px 20px;
		&:hover {
			height: 100%;
			.product__info-container {
				margin-top: 60px;
			}
		}
	}
	&__info-container {
		border-bottom: solid 1px rgba(255,255,255,0.3);
		margin-top: 0;
		transition: margin .3s;
	}
	&__info-list {
		list-style: none;
		padding: 0;
		margin: 0;
		margin-top: 20px;
	}
	&__info-item {
		font-size: 14px;
		color: #fff;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		&:before {
			content: '';
			width: 6px;
			height: 6px;
			display: inline-block;
			border: solid 2px #edc158;
			border-radius: 50%;
			margin-right: 10px;
		}
		&_no-bullet {
			padding-top: 15px;
			&:before {
				margin: 0;
				border: none;
				width: 0;
			}
		}
	}
	&__info-link {
		font-size: 14px;
		font-weight: 500;
		color: #fff;
		text-decoration: none;
	}
	&__footer {
		.clearfix();
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		background: #7dd5c9;
		padding: 20px;
		height: 70px;
		z-index: 5;
		.btn {
			float: right;
			margin-right: -11px;
		}
	}
	&__footer-container {
		float: left;
	}
	&__footer-container {
		.product__old-price + .product__price{
			line-height: 20px;
		}
	}
	&__old-price {
		font-size: 14px;
		font-weight: 500;
		color: #000;
		opacity: .7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		text-decoration: line-through;
		line-height: 12px;
	}
	&__price {
		font-size: 18px;
		font-weight: bold;
		color: #000;
		opacity: .9;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
		line-height: 30px;
	}
	&_white {
		background: #fff;
		.product__footer {
			background: #fff;
		}
		.wish-btn {
			border-color: #334c5d;
			transition: opacity .3s, background .3s, right .3s, border .3s, margin .3s;
			&:hover {
				opacity: .8;
				-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
				background: #334c5d;
				.wish-btn__text {
					color: #fff;
				}
			}
			&.checked {
				border-color: #fff;
				background: none;
				.wish-btn__text {
					color: #fff;
				}
			}
		}
		.wish-btn__text {
			color: #334c5d;
			transition: color .3s;
		}
	}
	&__page-block{
		background: #fff;
		padding: 30px;
		margin: 0 0 20px;
		h2,& h3{
			margin-top: 0;
		}
	}
}
.product__pb-title{
	margin: 0 0 20px;
	font-size: 24px;
	line-height: 1.3;
	font-weight: 300;
	color: #333;
}
.no-svg .product__flag {
    background: url('../img/png-icons/flag.png');
}