.steps {
	background: #fff;
	&__list {
		list-style: none;
		padding: 0;
		margin: 0;
		.clearfix();
	}
	&__item {
		float: left;
		width: 100% / 4;
		.clearfix();
		padding: 25px 0 20px;
		position: relative;
		font-size: 0;
		&:last-child {
			&:after {
				background: none;
			}
		}
	}
	&__item-arrow{
		background: url('../img/icons/big_arrow.svg');
		display: inline-block;
		width: 30px;
		height: 30px;
		position: absolute;
		top: 54%;
		right: -11px;
		margin-top: -15px;
	}
	&__img {
		position: absolute;
		left: 26px;
		top: 50%;
		margin-top: -20px;
		width: 40px;
		height: 40px;
		&_search {

		}
		&_cart {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
		&_manager {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
		&_rocket {
			width: 50px;
			height: 50px;
			margin-top: -25px;
		}
	}
	&__text {
		display: block;
		width: 100%;
		padding-left: 90px;
		margin: 0;
		font-size: 14px;
		line-height: 20px;
		opacity: .7;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
		padding-right: 15px;
	}
}

.no-svg .steps__item-arrow{
	background: url('../img/png-icons/icons/big_arrow.png');
}