.menu-dropdown{
	position: absolute;
	max-height: 0px;
	overflow: hidden;
	width: 100%;
	background: transparent;
	top: 50px;
	opacity: 0;
	padding-top: 10px;
	.transition(all 0.6s);
	.roboto();
	letter-spacing: 0.04em;
	font-weight: 400;
	z-index: 20;
	&.active{
		max-height: 800px;
		opacity: 1;
	}
	&__container{
		font-size: 0;
		display: none;
		width: 100%;
		background: #fff;
		padding: 26px 0 30px;
		vertical-align: top;
		position: relative;
		.clearfix();
		&.active{
			display: table;
		}
	}
	&__genre{
		font-size: 14px;
		display: table-cell;
		.clearfix();
		border-right: 1px solid #dbdbdb;
		vertical-align: top;
		position: relative;
		.clearfix();
		&.col-1{
			width: 20%;
			height: 100%;
			.menu-dropdown__column{
				width: 100%;
			}
		}
		&.col-2{
			width: 40%;
			.menu-dropdown__column{
				width: 50%;
			}
		}
		&.col-3{
			width: 60%;
			.menu-dropdown__column{
				width: 33.3%;
			}
		}
		.col-element{
			position: relative;
			.clearfix();
			& + .col-element{
				margin-top: 20px;
				padding-top: 20px;
				&:after{
					display: block;
					content: '';
					position: absolute;
					top: 0;
					left: 20px;
					right: 20px;
					height: 1px;
					background: #dbdbdb;
				}
			}
		}
		&_last{
			border-right: none;
		}
	}
	&__column{
		display: block;
		width: 100%;
		float: left;
		position: relative;
		padding: 0;
	}
	&__title{
		margin-top: 0;
		font-weight: 600;
		font-size: 14px;
		color: #334c5d;
		margin-bottom: 12px;
		padding: 0 20px 0 20px;
		text-transform: uppercase;
	}
	&__list{
		list-style-type: none;
		padding: 0;
		margin: 0;
	}
	&__item{
		margin-top: 0;
		font-size: 14px;

	}
	&__goTo{
		color: #000;
		background-color: transparent;
		display: block;
		padding: 9px 20px;
		.transition(background-color .4s);
		&:visited{
			color: #000;
		}
		&:hover{
			background-color: #f9c866;
		}
	}
}
.drodown-mask{
	top: 0;
	left: 0;
	position: fixed;
	width: 100vw;
	background: #334c5d;
	display: block;
	height: 0;
	z-index: 2;
	opacity: 0;
	.transition(opacity .6s);
	&.active{
		height: 100vh;
		opacity: 0.8;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
	}
}