.stars{
	position: relative;
	overflow: hidden;
	font-size: 0;
}
.stars_white{
	color: #fff;
}
.stars_orange{
	color: #F8C255;
}
.stars__item{
	cursor: pointer;
	display: inline-block;
	vertical-align: top;
	line-height: 0;
	position: relative;
	z-index: 1;
	.stars_disabled &{
		cursor: default;
	}
}
.stars__input{
	position: absolute;
	opacity: 0.01;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=1)";
	z-index: -1;
}
.stars__svg{
	fill: currentColor;
	stroke: currentColor;
}
.stars__input ~ .stars__item .stars__svg{
	fill: currentColor;
}
.stars__input:checked ~ .stars__item .stars__svg{
	fill: none;
}
.stars__input:checked + .stars__item .stars__svg{
	fill: currentColor;
}
.stars_white .stars__input:focus + .stars__item .stars__svg{
	fill: #eee;
}
.stars_orange .stars__input:focus + .stars__item .stars__svg{
	fill: darken(#F8C255, 15%);
}